        justify-content: center; }
    .sizes__right .sizes__h1 {
        left: 64px; }
    .sizes__left {
        max-width: 100%; }
    .sizes__left .btn {
        position: relative; }
    .sizes__left p {
        margin-bottom: 15px; }
    h2 {
        padding-left: 20px;
        &::after {
            left: -10px; } }
    .go-top {
        right: 15px;
        bottom: 15px; }
    .portofolio__gallery {
        justify-content: center; }
    .about .flex {
        justify-content: center; }
    .stages img {
