        height: 202px; }
    .about__video-link {
        max-width: 70px;
        height: 70px; }
    .about__video-link .obvodka {
        max-width: 70px;
        height: 70px; }
    .about__video-link .center {
        max-width: 30px;
        height: 30px; } }
